import React, { Component } from 'react'
import "../css/recharge.css"
import { Link, withRouter } from 'react-router-dom'

class Withdrawl extends Component {
    componentDidMount() {
        localStorage.setItem('nKey', 4)
    }

    componentWillUnmount = () => {
        this.setState = (state, callback) => {
            return;
        };
    }
    render() {
        return (
            <div className="containerwidth">
                {
                    Number(localStorage.getItem('userType')) === 0 ?
                        <div className="guestUserdisplay" style={{ marginTop: '10px' }}></div> :
                        <div className="recharge">
                            <div className="recharge-row">
                                <div className="price">
                                    <Link to="/withdrawl-calc/1" className="p_header">50000</Link>
                                    <p className="p_title">50,000,000</p>
                                    <Link to="/withdrawl-calc/1"></Link>
                                </div>
                                <div className="price">
                                    <Link to="/withdrawl-calc/2" className="p_header">10000</Link>
                                    <p className="p_title">10,000,000</p>
                                    <Link to="/withdrawl-calc/2"></Link>
                                </div>
                                <div className="price">
                                    <Link to="/withdrawl-calc/3" className="p_header">5000</Link>
                                    <p className="p_title">5,000,000</p>
                                    <Link to="/withdrawl-calc/3"></Link>
                                </div>
                            </div>
                            <div className="recharge-row">
                                <div className="price">
                                    <Link to="/withdrawl-calc/4" className="p_header">1000</Link>
                                    <p className="p_title">1,000,000</p>
                                    <Link to="/withdrawl-calc/4"></Link>
                                </div>
                                <div className="price">
                                    <Link to="/withdrawl-calc/5" className="p_header">500</Link>
                                    <p className="p_title">500,000</p>
                                    <Link to="/withdrawl-calc/5"></Link>
                                </div>
                                <div className="price">
                                    <Link to="/withdrawl-calc/6" className="p_header">100</Link>
                                    <p className="p_title">100,000</p>
                                    <Link to="/withdrawl-calc/6"></Link>
                                </div>
                            </div>
                        </div>
                }

            </div>
        )
    }
}

export default withRouter(Withdrawl)